  width: 420px;
  height: 420px;
  z-index: -1;
  
}

.picturediv {
  cursor: pointer;
  margin: 10px;
  padding:0px;
  width: 420px;
  height: 420px;
  overflow-y: hidden;
}

.picturediv:hover .section {
  cursor: pointer;
  -webkit-filter: brightness(0.5);
          filter: brightness(0.5);
  transition: 1s;
}

.picturediv:hover .picturetext {
  visibility: visible;
  opacity: 1;
  cursor: pointer;
  width: 420px;
  height: 320px;
   transition: opacity .2s, visibility .2s;
}

.picturetext {
  -webkit-transform: translate(-0%, -100%);
          transform: translate(-0%, -100%);
